<template>
	<view class="wrap">

		<view>
			<textarea class="input" placeholder="这一刻的想法" maxlength="-1" @input="getWords" />
			</view>
		<view class="imgs">
			<!-- <image src="http://localhost:8080/e393923c-98e9-47e6-81f7-8382b27d0b3c"></image> -->
			<button @click="upImg()" style="width: 100px;height: 100px;"><image src="../../static/img/add.png" style="width: 100rpx;height: 100rpx; margin-top: 50rpx;"></image></button>
		</view>
		<view class="publish">
			<button class="publishbtn" @click="publish()">发布想法</button>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				ideaWords: "",
				imgList:[],
				src:""
			}
		},
		computed:{
			// img(){
				
			// 	return this.imgList
			// }
		},
		methods: {
			getWords(event) {
				this.ideaWords = event.target.value;
			},
			upImg(){
				uni.chooseImage({
					    count: 6, //默认9
					    sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
					    sourceType: ['album'], //从相册选择
					success(res) {
						console.log(JSON.stringify(res.tempFilePaths));
						let list =[]
						for(let value of res.tempFilePaths){
							
							list.push(value.slice(5))
							console.log( res.tempFilePaths)
							
						}
						// this.imgList=res.tempFilePaths;
						
						  uni.previewImage({
						            urls: res.tempFilePaths,
						            longPressActions: {
						                itemList: ['发送给朋友', '保存图片', '收藏'],
						                success: function(data) {
						                    console.log('选中了第' + (data.tapIndex + 1) + '个按钮,第' + (data.index + 1) + '张图片');
						                },
						                fail: function(err) {
						                    console.log(err.errMsg);
						                }
						            }
						        });
						
					}
				})
			},
			publish(){
				let params={
					content:this.ideaWords,
					imageList:[""]
				}
				console.log(params);
				marchCircleList(params).then(res=>{
					console.log("发布成功")
				})
			}
		},
		created() {
		}
	}
</script>

<style scoped>
	/* 发布部分 */
	.publishbtn{
		border-radius: 40rpx;
		color: white;
		width: 500rpx;
		height: 80rpx;
		background-color: #007AFF;
		display: inline-block;
		text-align: center;
		line-height: 2.3;
		position: fixed;
		bottom: 10rpx;
		left: 120rpx;
	}
	.publishimg{
		width: 80rpx;
		height: 80rpx;
	}
	/* 图片部分 */
	uni-button{
		margin: 0 0;
	}
	.imgs{
		display: flex;
	
	}
	/* 文字部分 */
	.input {
		width: 100%;
		height: 300rpx;
		text-align: left;
		overflow: hidden;
		-webkit-line-clamp: 2;
		text-overflow: ellipsis;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		margin-bottom: 20rpx;
	}

	.wrap {
		padding: 20rpx;
	}
</style>
